import * as React from 'react'
import { Link } from 'gatsby'
import { container, gitIcon, aboutIcon, blogIcon, portfolioIcon, centerNav} from './centernav.module.css'
import { StaticImage } from "gatsby-plugin-image"

const CenterNav = ({callback}) => {

    const onTrigger = () => {
        callback();
    }

    return(
        <div className={container}>
            <div className={`${aboutIcon} ${centerNav}`}>
                <Link to="/about" onClick={onTrigger()}>
                    About
                </Link>
            </div>
            <div className={`${gitIcon} ${centerNav}` }>
                <Link to="https://github.com/gfreitag" onClick={onTrigger()}>
                    My Github
                </Link>
            </div>
            <div className={`${blogIcon} ${centerNav}`}>
                <Link to="/blog" onClick={onTrigger()}>
                    Blog
                </Link>
            </div>
            <div className={`${portfolioIcon} ${centerNav}`}>
                <Link to="/portfolio" onClick={onTrigger()}>
                    Portfolio
                </Link>
            </div>
        </div>
    )
}

export default CenterNav